<template>
    <form @submit="onSubmit" class="add-form" >
        <div class="form-control">
            <label>Task</label>
            <input  v-model="text" 
                    type="text" name="text" 
                    placeholder="Add Task" />
        </div>
        {{ text }}
        <div class="form-control">
            <label for="day">Day & time</label>
            <input
                    v-model="day"
                    type="date"
                    name="day"
                    placeholder="Add Day & Time"
            />
        </div>
        <div class="form-control form-control-check">
            <label for="reminder">Set reminder</label>
            <input v-model="reminder" type="checkbox" name="reminder" />

        </div>
        <div class="form-control">
            <label for="taskType">Task type</label>
            <select name="taskType" id="taskType" v-model="taskType" :required="true">
                <option disabled value="">Select type of task</option>
                <option value="DailyTasks">Daily Tasks</option>
                <option value="EmergencyTasks">Emergency Tasks</option>
                <option value="Challenges">Challenges</option>
                <option value="FutureTasks">Future Tasks</option>
            </select>
        </div>
        <input type="submit" vlaue="Save Task" class="btn btn-block">
    </form>
</template>

<script>
export default {
    name: 'AddTask',

    data(){
        return {
            text: '',
            day: '',
            reminder: false,
            taskType: ''
        }
    },

    methods: {
        onSubmit(e) {
            e.preventDefault()

            if(!this.text) {
                alert("Please add a task")
                return
            }

            const newTask = {
                text: this.text,
                day: this.day,
                reminder: this.reminder,
                taskType: this.taskType
            }

            this.$emit("add-new-task", newTask);
            this.text = ''
            this.day = ''
            this.reminder = false
            this.taskType = ''

        }
    }
}
</script>

<style scoped>
.add-form {
    margin-bottom: 40px;
}

.form-control {
    margin: 20px 0;
}

.form-control label {
    display: block;
}

.form-control input {
    width: 100%;
    height: 40px;
    margin: 5px;
    padding: 3px 7px;
    font-size: 17px;
}

.form-control-check {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.form-control-check label {
    flex: 1;
}

.form-control-check input {
    flex: 2;
    height: 20px;
}

.soft-delete {
    color: red;
}

</style>