<template>

    <div>
        <div class="w3-container">
        <h2>Tabs in a Grid</h2>

            <div class="w3-row">
                <button class="w3-bar-item w3-button" @click="openTasks('DailyTasks')">Daily Tasks</button>

                <button class="w3-bar-item w3-button" @click="openTasks('EmergencyTasks')">Emergency Tasks</button>

                <button class="w3-bar-item w3-button" @click="openTasks('Challenges')">Challenges</button>

                <button class="w3-bar-item w3-button" @click="openTasks('FutureTasks')">Future Tasks</button>
            </div>

            <Tasks  @toggle-reminder="toggleReminder" 
                    @delete-task="deleteTask" 
                    :tasks="tasks" />
            <!--
            <div v-if="toShow=='DailyTasks'">
                <DailyTasks  @toggle-reminder="toggleReminder" @delete-task="deleteTask" :tasks="tasks" />
            </div>
            <div v-if="toShow=='EmergencyTasks'">
                <EmergencyTasks @toggle-reminder="toggleReminder" @delete-task="deleteTask" :tasks="tasks" />
            </div>

            <div v-if="toShow=='Challenges'">
                <Challenges @toggle-reminder="toggleReminder" @delete-task="deleteTask" :tasks="tasks" />
            </div>

            <div v-if="toShow=='FutureTasks'">
                <FutureTasks @toggle-reminder="toggleReminder" @delete-task="deleteTask" :tasks="tasks" />
            </div>
            -->
        </div>
    </div>
</template>

<script>
import DailyTasks from "./TabsOfTasks/DailyTasks.vue";
import EmergencyTasks from "./TabsOfTasks/EmergencyTasks.vue";
import Challenges from "./TabsOfTasks/Challenges.vue";
import FutureTasks from "./TabsOfTasks/FutureTasks.vue";
import Tasks from './Tasks.vue';

export default {
    name:'DisplayTasks',
    templates: {
        DailyTasks,
        EmergencyTasks,
        Challenges,
        FutureTasks,
        Tasks
    },
    data() {
        return {
            toShow: String
        }
    },

    components: {
        DailyTasks,
        EmergencyTasks,
        Challenges,
        FutureTasks,
        Tasks
    },

    emits: [],

    props: {
            tasks: Array
    },

    methods: {
        openTasks(taskName) {
            this.toShow = taskName;
            this.$emit("filterTasks", taskName);
        },

        toggleReminder(taskId){
            this.$emit("toggle-reminder", taskId);
        },

        deleteTask(taskId){
            this.$emit("delete-task", taskId);
        }
    },
    emits: ['delete-task', 'toggle-reminder', "filterTasks"],

}
</script>
<style scoped>
@import 'https://www.w3schools.com/w3css/4/w3.css';

</style>