<template>
  <div class="container">
    <Header @toggle-add-task="toggleAddTask"
            title="Hello World"
            :showAddTask="showAddTask" />

    <div v-show="showAddTask">
      <AddTask @add-new-task="addNewTask" />
    </div>
    
    <Display-tasks  @filterTasks="filterTasks" 
                    @toggle-reminder="toggleReminder" 
                    @delete-task="deleteTask"
                    @openTasks="filterTasks"
                    :tasks="tasks" />
  </div>
    
</template>

<script>
import Header from './components/Header.vue';
import AddTask from './components/AddTask';
import DisplayTasks from './components/DisplayTasks.vue';


export default {
  name: 'App',
  components: {
    Header,
    AddTask,
    DisplayTasks
  },
  data() {
    return {
      tasks: [],
      showAddTask: false
    }
  },

  methods: {

    async fetchTasks() {
    const res = await fetch(`api/tasks`)

    const data = await res.json()

    return data
    },

    async fetchTask(id) {
    const res = await fetch(`api/tasks/${id}`)

    const data = await res.json()

    return data
    },

    async deleteTask(id) {
        if(confirm('Are you sure?')){
            const res = await fetch(`api/tasks/${id}`, {
            method: 'DELETE'
            })

        res.status === 200 ? (
            // just filter the array and "skip" the argument id
                this.tasks = this.tasks.filter((task) => task.id !== id )
            ) : alert('Error deleting task')
        }
    },

    async toggleReminder(id) {
        const taskToToggle = await this.fetchTask(id)
        const updTask = {...taskToToggle, reminder: !taskToToggle.reminder}

        const res = await fetch(`api/tasks/${id}`, {
            method: 'PUT',
            headers: {
            'Content-type': 'application/json'
            },
            body: JSON.stringify(updTask)
        })

        const data = await res.json()

        // when the loop find the id, map/copy everything as before but change the reminder in opposite
        this.tasks = this.tasks.map((task) =>  task.id === id ? {...task, reminder: data.reminder} : task )  
    },

    async addNewTask(newTask){
      const res = await fetch('api/tasks', {
        method: 'POST',
        headers: {
          'Content-type': 'application/json',
        },
        body: JSON.stringify(newTask),
      })
      
      const data = await res.json()

      this.tasks = [...this.tasks, data]
    },

    toggleAddTask() {
      this.showAddTask = !this.showAddTask;
    },

    async filterTasks(selectedTaskType){
      this.tasks = await this.fetchTasks();

      if(selectedTaskType == "DailyTasks")
        this.tasks = this.tasks.filter((task) => task.taskType == "DailyTasks")
      else if(selectedTaskType == "EmergencyTasks")
        this.tasks = this.tasks.filter((task) => task.taskType == "EmergencyTasks")
      else if(selectedTaskType == "Challenges")
            this.tasks = this.tasks.filter((task) => task.taskType == "Challenges")
      else if(selectedTaskType == "FutureTasks")
            this.tasks = this.tasks.filter((task) => task.taskType == "FutureTasks")
    },
  
  },

  async created() {
      this.tasks = await this.fetchTasks();
  }
}
</script>

