<template>
    <div>
        <Tasks  @toggle-reminder="toggleReminder" 
                @delete-task="deleteTask" 
                :tasks="tasks" />
    </div>
</template>

<script>
import Tasks from '../Tasks.vue';

export default {
    name: 'DailyTasks',
    
    templates: {
        Tasks
    },

    components: {
        Tasks
    },

    data() {
        return {

        }
    },

    props: {
        tasks: Array
    },

    methods: {
        toggleReminder(taskId){
            this.$emit("toggle-reminder", taskId)
        },

        deleteTask(taskId){
            this.$emit("delete-task", taskId)
        }
    },

    emits: ['delete-task', 'toggle-reminder'],

    
    
}
</script>